import 'assets/css/disabilityStatus.css';
import { Grid } from 'gridjs-react';
import 'gridjs/dist/theme/mermaid.min.css';
import { stationList, gridColumn } from 'app/data/deviceStatus/disabilityStatus/gridData';
import { NaverMap, RenderAfterNavermapsLoaded } from 'react-naver-maps';
import { useState } from 'react';

function DisabilityStatus() {
  const groupList = ['그룹1', '그룹2'];
  const [group, setGroup] = useState('');

  return (
    <div className="section__grid__wrap content disability__status">
      <section className="disability__list">
        <div className="section__inner">
          <select className="custom__select" onChange={(e) => setGroup(e.target.value)}>
            <option value="">전체 그룹</option>
            {groupList.map((obj, idx) => (
              <option key={obj} value={obj}>
                {obj}
              </option>
            ))}
          </select>
          <div className="map__grid__wrap">
            <div className="grid">
              <Grid
                sort="true"
                className="grid"
                data={stationList}
                columns={gridColumn}
                pagination={{
                  enabled: true,
                  limit: 10,
                }}
              />
            </div>
            <div className="map">
              <RenderAfterNavermapsLoaded
                ncpClientId="qelkdx0v6n"
                error={<p>Maps Load Error</p>}
                loading={<p>Maps Loading...</p>}
              >
                <NaverMap
                  mapDivId="map"
                  defaultCenter={{ lat: 37.49988, lng: 127.03856 }}
                  defaultZoom={16}
                />
              </RenderAfterNavermapsLoaded>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default DisabilityStatus;
